/* Base style for buttons */
.button {
  padding: 10px 20px;
  margin: 5px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

/* Larger padding and font size on smaller screens (e.g., mobile devices) */
@media (max-width: 600px) {
  .button {
    padding: 15px 30px;
    font-size: 18px;
    margin: 10px;
  }
}
